export default {
    data() {
        return {
            visible: false,
            obj: {
                title: 'modal title',
                content: "modal content",
                confirmText: "确定",
                cancelColor: "rgba(0,0,0,0.6)",
                confirmColor: "#3396fb",
                cancelText: "取消",
                showCancel : true,
            }
        }
    },

    watch: {
        modal() {
            this.initModal();
        }
    },

    mounted() {
        this.initModal();
    },

    props: {
        modal: {
            type: Object,
            default() {
                return {
                    title: 'modal title',
                    content: "modal content",
                    confirmText: "确定",
                    cancelColor: "rgba(0,0,0,0.6)",
                    confirmColor: "#3396fb",
                    cancelText: "取消",
                    showCancel : true,
                }
            }
        }
    },

    methods: {
        initModal() {
            let obj = this.obj;
            for (let k in this.modal) {
                if (this.modal[k] === undefined) continue;

                obj[k] = this.modal[k];
            }
            this.obj = obj;
        },

        show() {
            setTimeout(() => {
                this.visible = true
            }, 100);
        },

        hide() {
            setTimeout(() => {
                this.visible = false
            }, 100);
        },

        cancel() {
            this.hide();
            this.$emit("onModalChanged", false)
        },

        confirm() {
            this.hide();
            this.$emit("onModalChanged", true)
        }
    }
}
